import { createWebHistory, createRouter } from "vue-router";
import List from './components/ListVue.vue'
import Home from './components/HomeVue.vue'
import Detail from './components/DetailVue.vue'
import Author from './components/AuthorVue.vue'
import Comment from './components/CommentVue.vue'

const routes = [
  {
    path: "/list",
    component: List,
  },
  { 
    path: "/",
    component: Home,
  },
  {
    path: "/detail/:id",
    component: Detail,
    children: [
      {
        path: "author",
        component: Author,
      },
      {
        path: "comment",
        component: Comment,
      }
    ]
  }
];


const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 